import { instantiateXHyperLinks, XHyperLink } from '@engine/link';

export abstract class AbstractReportChart extends BI.Widget {
    public static EVENT_LOADED = 'EVENT_LOADED';

    protected chart: any;

    protected chartAttr: any;

    public props = {
        chartAttrGetter: async () => (<{
            chartAttr: any,
            chartID: string,
        } | {
            errorCode: string;
            errorMsg: string;
        }>{}),
        autoSize: true, // 图表是否根据容器大小自动设置尺寸，如果是false，则手动传入width和height。
        instantiateXHyperLinks: (javaScriptGroup: XHyperLink[]) => instantiateXHyperLinks(javaScriptGroup),
        wScale: 1,
        hScale: 1,
        fontScale: 1,
    }
    
    /**
     * 刷新，重绘但不重新请求数据
     */
    public abstract refresh(): void;

    /**
     * 重新请求数据
     */
    public abstract reload(): void;

    /**
     * 修改图表参数，用于联动
     * @param params 参数
     */
    public abstract changeParameter(params: { [key: string]: any; }): void;

    /**
     * resize接口
     * @param wScale 宽缩放比例
     * @param hScale 高缩放比例
     * @param fontScale 文字缩放比例
     */
    public abstract resize(wScale: number, hScale: number, fontScale: number): void;

    /**
     * 清除图表接口
     */
    public abstract remove(): void;

    /**
     * 销毁时的生命周期钩子
     */
    public destroy() {
        this.remove();
    }
}
